<template>
    <div class="slide_page">
        <div class="filter_hours">
            <div>{{airfilter}}</div>
            <span>滤网剩余(小时)</span>
        </div>
        <div style="margin: .8rem .24rem .4rem;">
            <mt-button type="primary" size="large">保存</mt-button>
        </div>
        <div style="margin: 0 .24rem;">
            <mt-button type="default" size="large">复位</mt-button>
        </div>
    </div>
</template>

<script>
    export default {
        name: "ResetAirFilter",
        data() {
            return {
                airfilter: 1666,
            }
        },
        methods: {
            // 打开 picker
            openPicker(picker) {
                this.$refs[picker].open();
            },
            // 开机时间
            confirm_onTime(time) {
                console.log(time);
                this.on_time = time.toString();
            },
            // 关机 时间
            confirm_offTime(time) {
                console.log(time);
                this.off_time = time.toString();
            },
        }
    };
</script>

<style scoped>
    .filter_hours {
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: center;
        width: 100%;
        height: 35%;
        background-color: #fff;
    }
    .filter_hours>div {
        color: #26a2ff;
        font-size: 1rem;
        font-weight: bold;
    }
    .filter_hours>span {
        padding: 5px;
        font-size: .32rem;
    }
</style>